



















































import {
    Component, Provide, Prop, Vue
} from 'vue-property-decorator';
import API from '@/api/api.js';

interface user {
    name: String,
    avatar: String,
    created_at: String,
    updated_at: String
}

interface category {
    id: Number,
    name: String,
    description: String
}

interface  detailData {
    id: Number,
    title: String,
    intro: String,
    body: String,
    img: String,
    user_id: Number,
    category_id: Number,
    reply_count: Number,
    view_count: Number,
    zan_count: Number,
    is_zan: Number,
    created_at: String,
    updated_at: String,
    user: user,
    category: category
}

@Component
export default class Detail extends Vue{
    @Provide() topicId: String = '';
    @Provide() commentList: Array<Object> = [];
    @Provide() detailMsg=<detailData>{};
    @Provide() showPage: Boolean = false;
    @Provide() dialogVisible: Boolean = false;
    @Provide() commentInput: String = '';


    created() {
        console.log(this.$route)
        let topicId = this.$route.params.id;
        this.topicId = topicId;
        API.getTopicDetail(topicId).then((res:any) => {
            console.log(res);
            if (res.code === 0) {
                this.detailMsg = res.data;
                this.showPage = true;
            } 
        });

        this.getCommentList();
    }
    // 评论
    goComment() {
        if (this.commentInput == '') {
            this.$message({
                message: '评论内容不能为空',
                type: 'warning'
            });
            return;
        }
        API.commentTopic(this.topicId, {
            comment_content: this.commentInput
        }).then((res:any) => {
            console.log(res);
            if (res.code === 0) {
                this.getCommentList();
                this.$message({
                    message: '评论成功',
                    type: 'success'
                });
            } else {
                this.$message.error('评论失败');
            }
            this.dialogVisible = false;
        })
        
    }

    //获取评论列表
    getCommentList() {
        API.getCommentList(this.topicId).then((res:any) => {
            console.log(res);
            if (res.code === 0) {
                this.commentList = res.data.data;
            } else {
                this.$message.error('评论列表获取失败!');
            }
            this.dialogVisible = false;
        })
    }

    goCategory() {
        if (this.detailMsg.category.id == 1) {
            this.$router.push('/').catch(err => {
                console.log(err);
            });
        } else {
            this.$router.push(`/category/${this.detailMsg.category.id}`).catch(err => {
                console.log(err);
            });
        }
        
    }
}
